import React, { useEffect } from "react";

const Weathercard = ({
  temp,
  humidity,
  pressure,
  weathermood,
  name,
  speed,
  country,
  sunset,
}) => {
  const [weatherState, setWeatheState] = React.useState("");

  useEffect(() => {
    if (weathermood) {
      switch (weathermood) {
        case "Clouds":
          setWeatheState("wi-day-cloudy");
          break;
        case "Haze":
          setWeatheState("wi-fog");
          break;
        case "Clear":
          setWeatheState("wi-day-sunny");
          break;
        case "Mist":
          setWeatheState("wi-dust");
          break;

        default:
          setWeatheState("wi-day-sunny");
          break;
      }
    }
  }, [weathermood]);

  // converting the seconds into time
  let sec = sunset;
  let date = new Date(sec * 1000);
  let timeStr = `${date.getHours()}:${date.getMinutes()}`;
  return (
    <>
      <article className="widget">
        <div className="weatherIcon">
          <i className={`wi ${weatherState}`}></i>
        </div>
        <div className="weather-content container">
            <div className="row single-item">
                <div className="col-md-4">
                    <div className="weatherInfo"></div>
                    <div className="temperature">
                        <span>{temp}&deg;</span>
                    </div>
                </div>
                <div className="col-md-4 description">
                    <div className="selimita">
                        <div className="weatherCondition">{weathermood}</div>
                        <div className="place">
                        {name}, {country}
                        </div>
                    </div>
                </div>
                <div className="date col-md-4"> {new Date().toLocaleString()} </div>
            </div>
        </div>

        {/* our 4column section  */}
        <div className="weather-footer container">
          <div className="row single-item">
            <div className="col-md-3">
                <i className={"wi wi-sunset"}></i>
                <p>
                    {timeStr} PM <br />
                    Sunset
                </p>
            </div>

            <div className="col-md-3">
                <i className={"wi wi-humidity"}></i>
                <p>
                    {humidity} <br />
                    Humidity
                </p>
            </div>
            <div className="col-md-3">
                <i className={"wi wi-rain"}></i>
                <p>
                    {pressure} <br />
                    Pressure
                </p>
            </div>
            <div className="col-md-3">
                <i className={"wi wi-strong-wind"}></i>
                <p>
                    {speed} <br />
                    Speed
                </p>
            </div>
          </div>
        </div>
      </article>
    </>
  );
};

export default Weathercard;
